body {
  width: 320px;
  display: flex;
  max-width: 100%;
  margin: 0 auto;
  box-shadow: 0 0 5px 1px #d3d3d3;
  font-family: "ubuntu-light", "Arial", sans-serif; }

#text {
  position: absolute;
  display: block;
  top: 0;
  padding-left: 10px; }

.select-language {
  display: inline-block;
  width: 160px;
  height: 34px;
  line-height: 30px;
  position: relative; }
  .select-language::before {
    content: ">";
    display: inline-block;
    background: white;
    position: absolute;
    right: -5px;
    top: 2px;
    z-index: 2;
    width: 30px;
    height: 26px;
    text-align: center;
    line-height: 26px;
    border: 2px solid #ddd;
    transform: rotate(90deg);
    cursor: pointer; }

.select-language input[name="list"]:not(:checked) ~ #text {
  color: black;
  background: white; }

.select-language input[name="list"]:checked ~ #text {
  background: transparent;
  color: transparent;
  z-index: 2; }

#select-language:disabled ~ .items #text {
  background: #eee; }

.items {
  display: block;
  min-height: 30px;
  position: absolute;
  border: 2px solid #ddd;
  overflow: hidden;
  width: 160px;
  cursor: pointer; }

#select-language:not(:checked) ~ .items {
  height: 30px; }

.items label {
  border-top: 2px solid #ddd;
  display: none;
  padding-left: 10px;
  background: white; }

.items label:hover {
  background: #eee;
  cursor: pointer; }

#select-language:checked ~ .items {
  padding-top: 30px; }

#select-language:checked ~ .items label {
  display: block; }

.items input:checked + label {
  display: block !important;
  border: none;
  background: white; }

#select-language ~ .bg {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 0;
  background: rgba(0, 0, 0, 0.4); }

a {
  display: block;
  text-decoration: none; }

input[type="radio"],
input[type="checkbox"] {
  display: none; }

label {
  display: block;
  font-weight: 700;
  font-size: 18px; }

label[for|="positive"]::before,
label[for|="negative"]::before,
label[for|="neutral"]::before {
  content: "";
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin: 0 23px 0 0;
  vertical-align: middle;
  background-color: #f2f2f2;
  border: 17px solid #f2f2f2; }

label[for|="positive"],
label[for|="negative"],
label[for|="neutral"] {
  margin-bottom: 15px; }
  label[for|="positive"]:hover::before,
  label[for|="negative"]:hover::before,
  label[for|="neutral"]:hover::before {
    background-color: #e5e5e5;
    border: 17px solid #e5e5e5; }
  label[for|="positive"]:active::before,
  label[for|="negative"]:active::before,
  label[for|="neutral"]:active::before {
    border: 17px solid #d5d5d5;
    background-color: rgba(3, 3, 3, 0.3); }

label[for|="bridge"]::before,
label[for|="mountain"]::before,
label[for|="park"]::before,
label[for|="rocks"]::before {
  content: "";
  display: inline-block;
  width: 56px;
  height: 56px;
  border-radius: 7px;
  margin-right: 16px;
  background-color: #f2f2f2;
  vertical-align: middle;
  border: 3px solid #ffffff; }

label[for|="bridge"]:hover::before,
label[for|="mountain"]:hover::before,
label[for|="park"]:hover::before,
label[for|="rocks"]:hover::before {
  background-color: #e5e5e5; }

label[for|="bridge"]:active::before,
label[for|="mountain"]:active::before,
label[for|="park"]:active::before,
label[for|="rocks"]:active::before {
  transition: all 0.3s;
  background-color: #d5d5d5; }

input:checked[type="radio"] + label::before {
  background-color: #000000; }

input:checked[type="checkbox"] + label::before {
  content: "";
  background-image: url("../img/tick.svg");
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: 50% 50%; }

input:checked[type="checkbox"] + label:active::before {
  opacity: 0.5; }

label[for|="radio"],
label[for|="checkbox"] {
  color: #333333;
  vertical-align: middle; }

@media (min-width: 768px) {
  label[for|="positive"] {
    margin: 33px 0 0 32px; }
  label[for|="negative"] {
    margin: 25px 0 0 32px; }
  label[for|="neutral"] {
    margin: 25px 0 0 32px; }
  body {
    width: 768px; } }

@media (min-width: 1200px) {
  body {
    width: 100%;
    max-width: 100% !important;
    margin: 0; } }

